'use client';

import { setToken } from '@/server/actions/auth';
import { setLocale } from '@/server/actions/locale';
import {
  ActionIcon,
  Button,
  Divider,
  Group,
  Menu,
  MenuDropdown,
  MenuItem,
  MenuTarget,
  Text,
  useMantineColorScheme,
} from '@mantine/core';
import { useHotkeys } from '@mantine/hooks';
import { useNotifications } from '@repo/hooks';
import {
  IconBell,
  IconLanguage,
  IconLogout,
  IconMessage,
  IconMoon,
  IconSun,
  IconUser,
} from '@tabler/icons-react';
import { useMutation } from '@tanstack/react-query';
import dayjs from 'dayjs';
import { useTranslations } from 'next-intl';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';

export function Header() {
  const router = useRouter();
  const t = useTranslations();
  const n = useNotifications();
  const [datetime, setDatetime] = useState(new Date());
  const { toggleColorScheme, colorScheme } = useMantineColorScheme();

  useHotkeys([['mod+J', () => toggleColorScheme()]]);

  useEffect(() => {
    const interval = setInterval(() => setDatetime(new Date()), 1000);
    return () => clearInterval(interval);
  }, []);

  const logoutMut = useMutation({
    mutationFn: () => setToken(''),
    onSuccess: () => {
      router.push('/accounts/login');
      n.success(t('logoutSuccess'));
    },
  });

  return (
    <Group mx="xl" h="100%" justify="space-between">
      <Text ff="Roboto" fw={500}>
        {dayjs(datetime).format('HH:mm A')}
      </Text>

      <Group gap="sm">
        <ActionIcon size="lg" variant="natural">
          <IconMessage />
        </ActionIcon>

        <ActionIcon size="lg" variant="natural">
          <IconBell />
        </ActionIcon>

        <Divider orientation="vertical" />

        <Menu>
          <MenuTarget>
            <ActionIcon size="lg" variant="natural">
              <IconUser />
            </ActionIcon>
          </MenuTarget>

          <MenuDropdown miw={160}>
            <MenuItem
              color="red"
              leftSection={<IconLogout size={18} />}
              onClick={() => logoutMut.mutate()}
            >
              {t('logout')}
            </MenuItem>
          </MenuDropdown>
        </Menu>

        <Divider orientation="vertical" />

        <ActionIcon size="lg" variant="natural" onClick={toggleColorScheme}>
          {colorScheme === 'light' ? <IconMoon /> : <IconSun />}
        </ActionIcon>

        <Divider orientation="vertical" />

        <Menu>
          <MenuTarget>
            <Button miw={72} size="compact-sm" radius="100vw">
              <IconLanguage size={18} />
            </Button>
          </MenuTarget>

          <MenuDropdown>
            <MenuItem leftSection="En" onClick={() => setLocale('en')}>
              English
            </MenuItem>

            <MenuItem leftSection="Ar" onClick={() => setLocale('ar')}>
              العربية
            </MenuItem>
          </MenuDropdown>
        </Menu>
      </Group>
    </Group>
  );
}
